        author_association: "CONTRIBUTOR",
        active_lock_reason: null,
        body: "### Bug report\r\nWhen ToolBar has initially an overflow button, hide() method does not hide a buttonGroup inside the overflow. Buttons on the contrast are hidden as expected.\r\n\r\n### Reproduction of the problem\r\n1. Load the following [Dojo snippet](https://dojo.telerik.com/@Dimitar-Goshev/EQaXObAs), then shrink the right side so that the second and third button groups would be in overflow dropdown.\r\n2. Run the Dojo\r\n3. Expand the overflow dropdown.\r\n\r\n### Current behavior\r\nThe third button group is not hidden in the overflow by calling the hide() method. It is not hidden even if we call the hide() method with the id of the overflow group item.\r\nAs a side note, resizing the toolbar keeps the group visible in the overflow, even if there is enough space to get out of the overflow and be hidden.\r\n\r\n### Expected/desired behavior\r\nThe third button group is hidden by calling the hide() method.\r\n\r\n### Environment\r\n* **Kendo UI version:** 2022.1.301\r\n* **Browser:** all",
