        author_association: "CONTRIBUTOR",
        active_lock_reason: null,
        body: "### Bug report\r\n\r\nThe download functionality of the PDFViewer component and when downloading PDF using kendo.saveAs() method doesn't work on some iOS touch devices.\r\n\r\n### Reproduction of the problem\r\n1. Open the [PDFViewer](https://demos.telerik.com/kendo-ui/pdfviewer/index) demo or the[ PDF Export](https://demos.telerik.com/kendo-ui/pdf-export/index) Demo\r\n2. Try to export the file to PDF on iOS device\r\n\r\n**Note**: On some iOS touch devices the PDF is exported as expected, but on others, nothing happened.\r\n\r\n### Current behavior\r\nCurrently, on some iOS devices, the PDF is exported, on others, it is exported, but there is a warning  'This form is not secure, are you sure you want to submit it'. After approval, the file is downloaded. On some iOS devices after clicking the button to download the PDF file nothing happens.\r\n\r\n### Expected/desired behavior\r\nIt should be possible to download the PDF file on all iOS devices. \r\n\r\nReported in ticket ID: **1556787**\r\n\r\n### Environment\r\n\r\n* **Kendo UI version:** 2022.1.301\r\n* **Browser:** [iOS XX Safari] \r\n\r\n\r\n",
