        updated_at: "2022-03-08T08:20:41Z",
        closed_at: null,
        author_association: "CONTRIBUTOR",
        active_lock_reason: null,
        body: "### Bug report\r\n\r\nWhen a value in the TreeList nested in the Gantt is edited and is initially selected using the mouse, the save event is fired, but the **e.values** is _undefined_. Also, the value is changed back to the initial one.\r\n\r\n### Reproduction of the problem\r\n1. Open the Dojo - [https://dojo.telerik.com/@NeliKondova/arUgAguv](https://dojo.telerik.com/@NeliKondova/arUgAguv)\r\n2. Click in the 'Task1' cell to edit and mark the entire text using the mouse\r\n3. Type some text and press 'Enter' - The issue will not be replicated in case the edited mode is exited through clicking in another cell, but only by pressing Enter.\r\n[screencast](https://www.screencast.com/t/rhGQWwsoX)\r\n\r\n### Current behavior\r\nThe Edit and Save events are fired. The value of the **e.values** in the save events is _undefined_. The value of the cell is changed back to the initial one. \r\n\r\nWhen the value of the cell is not marked initially with the mouse, but the user starts typing immediately after clicking in the cell the issue is not replicated. Also, only the Save event is fired and the new value is present in **e.values** \r\n\r\nNote: The issue might be related to the following behavior in the TreeList: When the steps described above are followed in the TreeList - the save event is fired, but actually the edited mode is not exited - [Dojo](https://dojo.telerik.com/@NeliKondova/UyOQOhOk)\r\n\r\n### Expected/desired behavior\r\nThe behavior should be consistent. The same events should be fired in case the edited mode is exited by pressing 'Enter' and by clicking in another cell and e.values should contain the updated value.\r\n\r\n### Environment\r\n\r\n* **Kendo UI version:** 2022.1.301.\r\n* **Browser:** [all ] \r\n\r\n\r\n",
