        author_association: "NONE",
        active_lock_reason: null,
        body: "#### Bug report\r\nThe current version of Kendo UI no longer allows you to align column headings as described in the demo when sorting is enabled\r\n\r\n#### Reproduction\r\n\r\nhttps://demos.telerik.com/kendo-ui/grid/cell-alignment\r\nYou can see an example of it on your own demo site (link above) - Ship City header should be centered but isn't.\r\n\r\n{\r\n    field: 'ShipCity',\r\n    title: 'Ship City',\r\n    headerAttributes: { style: 'text-align: center' },\r\n    attributes: { style: 'text-align: right' }\r\n}\r\nhttps://dojo.telerik.com/@protest/ovedihat\r\n\r\nExpected/desired behavior\r\nheaderAttributes: { style: 'text-align: center' } shall apply the stiles correctly\r\n\r\nWorkaround\r\nA workaround at this time is applying the following style:\r\n\r\n```\r\n<style>\r\n      th.custom>.k-cell-inner>.k-link {\r\n        justify-content: right;\r\n      }\r\n<style>\r\n```\r\nhttps://dojo.telerik.com/uFivaSer\r\n\r\n#### Environment\r\nKendo UI version: 2022.1.119\r\nBrowser: [all]\r\n",
