        body: "### Bug report\r\n\r\nUsing Kendo UI in an Angular Project throws a 'Cannot read properties of undefined (reading 'options')' error in kendo.numerictextbox.js:394:1\r\n\r\n### Reproduction of the problem\r\nhttps://github.com/jvalkeejarvi/kendo-numerictextbox-angular-bug\r\n\r\nOpen grid column filter menu\r\n\r\nIssue is very similar to #6735 \r\n\r\n### Expected/desired behavior\r\nNo error should be thrown\r\n\r\n### Environment\r\n\r\n* **Kendo UI version:** 2022.1.301 (happens also with 2022.1.119)\r\n* **jQuery version:** 3.5.1\r\n* **Browser:** Chrome 99 (probably happens on every browser)\r\n\r\n\r\n",
