        body: "### Bug report\r\nWhen the SheetsBar contains more sheets pressing the SheetsBar left/right navigation arrow will close the formula editor.\r\n\r\n### Reproduction of the problem\r\n* Run the following Dojo: https://dojo.telerik.com/ETOsIzUP\r\n* In cell A1 press '=' key (to open edit formula mode)\r\n* Press scroll right button on SheetsBar (to scroll to the last Sheet)\r\n* Formula editing mode stops and 'A1' cell contains '=' char\r\n\r\n### Expected/desired behavior\r\nFormula editing mode should remain when pressing the scroll arrow.\r\n\r\n### Environment\r\n* **Kendo UI version:** 2022.1.119\r\n* **Browser:** [all] ",
