        author_association: "NONE",
        active_lock_reason: null,
        body: "### Bug report\r\n\r\nMultiSelect tags are not removed after dataSource filtering when item(s) are selected after manual input.\r\n\r\n### Reproduction of the problem\r\nDojo: http://dojo.telerik.com/otiNEYUD/2\r\n\r\nClick on the first multiSelect, select 'kitchen'. Click on second multiSelect, type 'ta', select 'table'. Go back to first multiSelect, click 'x' to removed 'kitchen'. The 'table' tag on the second multiSelect is still visible, though no values are available. See console log to confirm no value is selected. Clicking on the 'x' of the still present table tag produces an error: 'Uncaught TypeError: l.select(...).done is not a function'\r\n\r\n### Current behavior\r\nIt should be noted that the intention is to have the available values of the second mulitSelect be dependent on selected values of the first (ignoring the initial load). If nothing is selected in the first, the second is intended to be empty (and not have any tags).\r\n\r\nThis issue only occurs if the user manually types to further filter the values down and selects an item. If no manual input occurs, tags are cleared as expected.\r\n\r\nThis is similar to an issue that was corrected previously when programmatically clearing all values of a multiSelect.\r\nSee: https://github.com/telerik/kendo-ui-core/issues/3788\r\nIn this case I don't want to clear all values, just the ones not available due to filtering of the data source. \r\n\r\nI've tried clearing all filters first with multiSelect.dataSource.filter({}), then passing in the new filter object, but the results are same.\r\n\r\n### Expected/desired behavior\r\nTags should be removed when selected item(s) are no longer available due to dataSource filtering.\r\n\r\n### Environment\r\n\r\n* **Kendo UI version:** 2018.2.516 (also tried with 2022.1.119 with same results)\r\n* **jQuery version:** 3.3.1\r\n* **Browser:** [all] \r\n\r\n\r\n",
