        author_association: "NONE",
        active_lock_reason: null,
        body: "### Bug report\r\n\r\nAfter updating from the 2021.2.616 version to the latest version (2022.1.119), I noticed 2 issues that have broken my application.\r\n\r\n**1) MaskedTextBox does not show an exclamation mark anymore when in an invalid state.**\r\n\r\nThe reason for that, I believe, is a behaviour change on the _span.k-input-validation-icon_ element that is placed right after the input element. Now (but not before), even when the MaskedTextBox is in an invalid state, a _k-hidden_ class is placed on the span icon element, hiding it from the DOM. If I manually remove this class, the exclamation mark is shown again.\r\n\r\nThis bug can be reproduced right on the documentation: https://demos.telerik.com/aspnet-core/maskedtextbox/validation?autoRun=true&theme=bootstrap-main . Notice that the phone number field does not show an exclamation mark when in an invalid state.\r\n\r\nBut there is more.\r\n\r\n**2) Change on the red border color triggering behaviour**\r\n\r\nBefore, when in an invalid state, the MaskedTextBox border color changed to red when a _k-state-invalid_ class was added to the input element. But now, the border only changes to red when the wrapper element (the one with the _k-maskedtextbox_ class) has a _k-invalid_ class.\r\n\r\nThis has completely broken the desired behaviour of the MaskedTextBox when using the 'for' Model Validation tag helper. For instance, if we bind the MaskedTextBox to a required field (using the _for_ tag helper) and this field is not filled by the user on post, the MaskedTextBox only adds a _k-state-invalid_ class to the input element, but DOES NOT add a _k-invalid_ class to the wrapper element. The result: border color does not change.\r\n\r\n**For Feature Requests - please submit those into [our feedback portal](https://www.telerik.com/support/feedback)**\r\n\r\n### Environment\r\n\r\n* **Kendo UI version:** 2022.1.119\r\n* **jQuery version:** 3.6.0\r\n* **Browser:** [Chrome XX | Firefox XX ] \r\n\r\n\r\n",
