        active_lock_reason: null,
        body: "### Bug report\r\n\r\nUsing Kendo UI in an Angular Project throws a 'Cannot read properties of undefined (reading 'options')' error in kendo.colorpicker.js.\r\n\r\n**Regression with version 2022.1.119**\r\n\r\n### Reproduction of the problem\r\n\r\n1. Open the attached project.\r\n2. Run npm install\r\n3. Run npm run start\r\n\r\n### Current behavior\r\nAn error is thrown on line 279 in kendo.colorpicker.js - this is undefined.\r\n\r\n### Expected/desired behavior\r\nNo error should be thrown\r\n\r\n### Environment\r\n\r\n* **Kendo UI version:** 2022.1.119\r\n* **Browser:** [all] \r\n\r\n[issue-kendo-ui.zip](https://github.com/telerik/kendo-ui-core/files/8096889/issue-kendo-ui.zip)\r\n\r\n",
