        author_association: "NONE",
        active_lock_reason: null,
        body: "### Bug report\r\n\r\nThe sorted kendo column menu items are not persisted when hovering away from the column pop up. They sort correctly when I click the button and hover over to the list. However when I hover away from the column list the it closes(as expected) but when I hover back on the items are no longer sorted. Its like if the Open function only gets called once. Is there another function I should  attach an event to?\r\n\r\nI am using Kendo UI Jquery version 2021.2.511\r\n\r\nI have posted below the sample code to initialize the kendo column button, and the custom sorting function.\r\n\r\nAdding the columnMenu.sort propert does not allow for sorting.\r\n\r\n\r\n`function ESNReviewShowColumnSelections() {\r\n    var grid = $('#MyGrid').data('kendoGrid');\r\n\r\n    $('#MyButton').kendoColumnMenu({\r\n        filterable: false,\r\n        sortable: false,\r\n        dataSource: grid.dataSource,\r\n        columns: true,\r\n        owner: grid,\r\n        init: function (e) {\r\n            sortColumnMenu(e);\r\n        },\r\n        open: function (e) {\r\n            sortColumnMenu(e);\r\n        },\r\n    });\r\n}`\r\n\r\n`function sortColumnMenu(e) {\r\n\r\n    var list = e.container.find('.k-columns-item ul')\r\n    var items = list.find('li');\r\n\r\n    items.each(function (x, y) {\r\n        $(y).removeClass('k-first k-last')\r\n    })\r\n\r\n    items.sort(function (a, b) {\r\n        a = $(a);\r\n        b = $(b);\r\n\r\n        //var firstText = a.find('input[data-field]').attr('data-field');\r\n        //var secondText = b.find('input[data-field]').attr('data-field');\r\n        var firstText = a.find('input[data-field]').attr('title').toUpperCase();\r\n        var secondText = b.find('input[data-field]').attr('title').toUpperCase();\r\n\r\n        return ((firstText < secondText) ? -1 : ((firstText > secondText) ? 1 : 0));\r\n    })\r\n\r\n    items.first().addClass('k-first');\r\n    items.last().addClass('k-last');\r\n\r\n    items.each(function (y, x) {\r\n        list.append($(x));\r\n    })\r\n}`",
