        author_association: "NONE",
        active_lock_reason: null,
        body: "### Regression \r\n\r\nIn the Grid part, after changing the percentage of a child, its parent remains without a change in the percentage value.\r\n\r\n### Reproduction of the problem\r\n\r\n1. Open [this demo](https://demos.telerik.com/aspnet-mvc/gantt).\r\n2. On the left side(Grid part) update the '**Complete**' value of a child.\r\n\r\n### Current behavior\r\nThe parent of the updated child remains with the same percentage of its '**Complete**' field.\r\n\r\n### Expected/desired behavior\r\nThe parent should be updated after the change in its child node\r\n\r\n### Environment\r\n\r\n* **Kendo UI version:** 2022.1.119\r\n* **Browser:** [all]\r\n\r\n\r\n",
