        updated_at: "2022-02-08T14:45:42Z",
        closed_at: null,
        author_association: "NONE",
        active_lock_reason: null,
        body: "### Bug report\r\n\r\nThe Telerik UI for ASP.NET Core version of the Editor's ImageBrowser [Transport](https://docs.telerik.com/aspnet-core/api/Kendo.Mvc.UI.Fluent/EditorFileBrowserSettingsBuilder#transportsystemactionkendomvcuifluenteditorfilebrowsertransportsettingsbuilder) CRUD methods does not support the .Data(string handler) in order to send additional parameters. This is supported for the available ImageBrowser CRUD methods outside the Transport configuration.\r\n\r\n### Reproduction of the problem\r\n\r\nCurrently, it is not possible to pass additional data within the Transport configuration's CRUD methods when using the Telerik UI for ASP.NET Core HTML Helper. \r\n\r\nFor example:\r\n\r\n```\r\n@(Html.Kendo().Editor()\r\n    .Name('editor')\r\n    .ImageBrowser(imageBrowser => imageBrowser\r\n        .Transport(t => {\r\n            t.Read(r=>r.Url('/Editor/ImageBrowser?handler=Read').Data('dataHandler'));\r\n            t.Create(c => c.Url('/Editor/ImageBrowser?handler=Create').Data('dataHandler'));\r\n            t.Destroy(d=>d.Url('/Editor/ImageBrowser?handler=Destroy').Data('dataHandler'));                \r\n        })\r\n        .Schema(schema => schema\r\n        .Model(model => model.Fields(fields =>\r\n            {\r\n             fields.Name(name => name.Field('Name'));\r\n             fields.Size(size => size.Field('Size'));\r\n             fields.Type(type => type.Field('EntryType'));\r\n            })\r\n        ))\r\n    )\r\n)\r\n```\r\nIn the Kendo UI for jQuery suite, we can pass additional parameters within the transport configuration as follows:\r\n\r\n```\r\n$('#editor').kendoEditor({\r\n  imageBrowser: {\r\n    transport: {\r\n      read: {\r\n        data: {\r\n          id: 42,\r\n          name: 'John Doe'\r\n        }\r\n      }\r\n    }\r\n  }\r\n});\r\n```\r\n### Current behavior\r\n\r\nIt is not possible to pass additional data within the Transport configuration when using the Telerik UI for ASP.NET Core HTML Helper.\r\n\r\n### Expected/desired behavior\r\nIt should be possible to pass additional data within the Transport configuration when using the Telerik UI for ASP.NET Core HTML Helper.\r\n\r\n### Environment\r\n\r\n* **Kendo UI version:** 2022.1.119\r\n* **Browser:** [all] \r\n",
