        closed_at: "2022-02-16T21:21:31Z",
        author_association: "NONE",
        active_lock_reason: null,
        body: "### Bug report\r\n\r\nWhen columns.filterable.ui function is used the rendered inner input misses 'k-input-inner' class\r\n\r\n### Reproduction of the problem\r\nOpen this Dojo example - https://dojo.telerik.com/ejUQoSex\r\nOpen the filter menu\r\n\r\n![image](https://user-images.githubusercontent.com/46747298/152934944-d985ac7d-7771-4279-b7de-ee2fc496e57c.png)\r\n\r\n### Expected/desired behavior\r\nThe rendered DateTimePicker shall be according to the new rendering\r\n\r\nTicketID: 1551894\r\n\r\n\r\n### Workaround\r\n\r\nAdd the 'k-input-inner' class manually to the element.\r\n\r\n```javascript\r\n      ui: function(element) {\r\n        element.kendoDateTimePicker(); // initialize a Kendo UI DateTimePicker\r\n\t\r\n        setTimeout(function() {\r\n          $(element).addClass('k-input-inner');\r\n        });\r\n      }\r\n```\r\n\r\n### Environment\r\nKendo UI version: 2022.1.119\r\nBrowser: [all]",
