        closed_at: null,
        author_association: "NONE",
        active_lock_reason: null,
        body: "### Bug report\r\n\r\nWhen the DatePicker is configured as an [editor](https://docs.telerik.com/kendo-ui/api/javascript/ui/form/configuration/items#itemseditor) in the form, the editor does not provide an enable configuration option through the [editorOptions](https://docs.telerik.com/kendo-ui/api/javascript/ui/form/configuration/items#itemseditoroptions). It would be useful if the DatePicker provides an enable configuration that can be configured through the editor options.\r\n\r\n### Reproduction of the problem\r\n\r\n1. Run this [dojo](https://dojo.telerik.com/@alexander64836/uweQefik/2)\r\n2. Notice how the DatePicker is not disabled\r\n\r\n### Current behavior\r\n\r\nThe DatePicker Editor cannot be enabled or disabled through the editor options.\r\n\r\n### Expected/desired behavior\r\nThe DatePicker Editor should be enabled or disabled through the editor options.\r\n\r\n### Environment\r\n\r\n* **Kendo UI version:** 2022.1.119\r\n\r\n",
