        author_association: "NONE",
        active_lock_reason: null,
        body: "### Bug report\r\nFor the Kendo **Dropdowntree**  with the `checkbox` option set to true, **click** on any label to select the same, then the label will be highlighted. Later when we click on the same element, the selection isn't toggled (highlight is on).\r\nBut when we click the other element and click the earlier one, the selection toggle works.\r\n**Regression since 2022.1.119**\r\n\r\n### Reproduction of the problem\r\nDojo: https://dojo.telerik.com/okIsUSoK\r\n\r\n### Current behavior\r\nClicking on the highlighted label in **dropdowntree** widget with `checkbox` set to `true`, toggling of selection doesn't happen. We need to click on the checkbox to toggle the selection.\r\n\r\n### Expected/desired behavior\r\nI think we must be able to toggle the node selection irrespective of the highlight either by clicking the checkbox or label.\r\nExpected behavior is reproducible with 2021.3.1207 - [Dojo](https://dojo.telerik.com/@Dimitar-Goshev/icICevaZ)\r\n\r\n### Environment\r\n* **Kendo UI version:** 2022.1.119\r\n* **jQuery version:** 1.12.4\r\n* **Browser:** '5.0 (Windows NT 10.0; Win64; x64) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/97.0.4692.99 Safari/537.36'",
