        comments: 1,
        created_at: "2022-02-07T08:00:56Z",
        updated_at: "2022-02-15T09:44:54Z",
        closed_at: "2022-02-15T09:44:51Z",
        author_association: "NONE",
        active_lock_reason: null,
        body: "### Bug report\r\n\r\nWhen CheckBox is initialized by using Html helper in Telerik UI MVC app, the.HtmlAttributes() configuration applies the attributes to the hidden input field instead of the Checkbox. Regression in R1 2022.\r\n\r\n### Reproduction of the problem\r\n1. Sample Checkbox configuration : \r\n@Html.Kendo().CheckBox().Name('mybox').HtmlAttributes(new { onclick = 'MyHandler()' });\r\n\r\n2. The attached attributes are applied to the hidden input field, not to the checkbox itself.\r\n\r\n### Current behavior\r\n\r\nThe attached attributes are applied to the hidden input field, not to the checkbox itself.\r\n\r\n### Expected/desired behavior\r\nThe attached attributes are applied to the checkbox.\r\n\r\n### Environment\r\n\r\n* **Kendo UI version:** 2022.1.119\r\n* **Browser:** [all ] \r\n\r\n\r\n",
