        author_association: "NONE",
        active_lock_reason: null,
        body: "Add 'k-no-click' class to readonly state in DropDownList & DropDownTree to prevent clicking on the component and make it look in a readonly state\r\n\r\n### Current behavior\r\n.k-no-click class is missing and the component is clickable\r\n\r\nSee [dojo](https://dojo.telerik.com/OboKOlIj)\r\n\r\nClick 'Run'\r\nCheck element span class 'k-widget k-dropdown', 'k-no-click' is missing\r\nYou can refer to MultiSelect readonly state\r\n\r\n### Expected/desired behavior\r\nReadonly state of DropDownList & DropDownTree class must be 'k-widget k-dropdown k-no-click'.\r\n\r\n### Environment\r\n\r\n* **Kendo UI version:** all\r\n\r\n\r\n\r\n",
