        author_association: "CONTRIBUTOR",
        active_lock_reason: null,
        body: "### Bug report\r\n\r\nWhen the [columns.sortable.initialDirection](https://docs.telerik.com/kendo-ui/api/javascript/ui/grid/configuration/columns.sortable#columnssortableinitialDirection) configuration is set it is not respected\r\n```\r\ncolumns: [{\r\n      field: 'id',\r\n      sortable: {\r\n        initialDirection: 'desc'\r\n      }\r\n    }],\r\n```\r\n\r\n### Reproduction of the problem\r\n1. Run [this dojo](https://dojo.telerik.com/@AleksandarEvangelatov/IruXeDUP)\r\n\r\n### Current behavior\r\nNote the userId column is not sorted in descending order\r\n\r\n### Expected/desired behavior\r\nColumn should be sorted in descending order\r\n\r\n### Workaround\r\nUse the dataSource [sort](https://docs.telerik.com/kendo-ui/api/javascript/data/datasource/configuration/sort) configuration option to set the sort order - [example](https://dojo.telerik.com/@AleksandarEvangelatov/AQIGaXId)\r\n\r\n### Environment\r\n\r\n* **Kendo UI version:** 2022.1.119\r\n* **Browser:** [all] \r\n\r\n\r\n",
