        author_association: "NONE",
        active_lock_reason: null,
        body: "### Bug report\r\n\r\nWhen the MaskedTextBox is configured as an [editor](https://docs.telerik.com/kendo-ui/api/javascript/ui/form/configuration/items#itemseditor) in the form the editor does not provide an enable configuration option. It would be useful if the MaskedTextBox provides a enable configuration option that can be configured through the [editor options](https://docs.telerik.com/kendo-ui/api/javascript/ui/form/configuration/items#itemseditoroptions) similarly to the TextBox editor.\r\n\r\n\r\n### Reproduction of the problem\r\n\r\n1. Run this [dojo](https://dojo.telerik.com/@alexander64836/IyUPoRUC/22)\r\n2. Notice how the MaskedTextBox is not disabled\r\n\r\n### Current behavior\r\nThe MaskedTextBox cannot be disabled or enabled through the editor options.\r\n\r\n### Expected/desired behavior\r\nThe MaskedTextBox should be enabled or disabled through the editor options.\r\n\r\n### Environment\r\n\r\n* **Kendo UI version:** 2022.1.119\r\n* **Browser:** [all] \r\n\r\n\r\n",
