        author_association: "CONTRIBUTOR",
        active_lock_reason: null,
        body: "### Bug report\r\nWhen NVDA is on, for selection does not go beyond the optionLabel, when it is displayed as selected\r\n\r\n### Reproduction of the problem\r\n* Turn on NVDA\r\n* Open the following demo in Firefox: https://demos.telerik.com/kendo-ui/dropdownlist/cascadingdropdownlist\r\n* Using the Tab key focus the first DDL\r\n* Press Alt+Down Arrow to open the widget popup\r\n* Press the Down Arrow - the focus and selection does not move to the next option in the list\r\n\r\n### Expected/desired behavior\r\nThe focus and selection should move beyond the optionLabel item in the list\r\n\r\n### Environment\r\n* **Kendo UI version:** 2022.1.119\r\n* **Browser:** [Firefox,Chrome] ",
