        author_association: "NONE",
        active_lock_reason: null,
        body: "**Describe the bug**\r\n\r\nIn version 2022.1.119 when the Editor is placed in the same container with another element that also has width:100% the Editor is visibly wider. \r\n\r\n**To reproduce**\r\n1. Run [this Dojo](https://dojo.telerik.com/@Stoyan/aGiHUVuk) or [this REPL](https://netcorerepl.telerik.com/QwYPdFvX20hDN78f26)\r\n1. Inspect the Editor's wrapper \r\n\r\n**Expected behavior**\r\nThe Editor's width should not be overridden\r\n\r\n**Screenshots**\r\n<img width='463' alt='image' src='https://user-images.githubusercontent.com/35375994/151858519-215085d8-2d67-4984-9fa1-645e9904cc69.png'>\r\n\r\n**Affected package (please remove the unneeded items)**\r\n* theme-default\r\n* theme-bootstrap\r\n\r\n\r\n**Affected suites (please remove the unneeded items)**\r\n* Kendo UI for jQuery\r\n\r\n**Affected browsers (please remove the unneeded items)**\r\n* All\r\n\r\n**Build system information (please remove the unneeded items)**\r\n* Not Applicable\r\n\r\n**Additional context**\r\nIssue not reproducible with Less Themes\r\n\r\n\r\n",
