        active_lock_reason: null,
        body: "### Bug report\r\n\r\nStarting from v2022.1.119 the scrollbar of the View Html tool of the Editor is missing. \r\n\r\n### Reproduction of the problem\r\n1. Run [this Dojo](https://dojo.telerik.com/@Stoyan/oBAbOKUS) or the [All Tools Demo](https://demos.telerik.com/kendo-ui/editor/all-tools)\r\n2. Click on the 'View Html' Tool\r\n\r\n### Current behavior\r\n<img width='476' alt='image' src='https://user-images.githubusercontent.com/35375994/151826835-bf2a333d-5e0b-4405-856c-8b5cf651e65b.png'>\r\n\r\n\r\n### Expected/desired behavior\r\nThe textarea of the View Html's popup should be scrollable\r\n\r\n### Environment\r\n\r\n* **Kendo UI version:** 2022.1.119\r\n* **Browser:** [all] \r\n\r\n\r\n",
