        author_association: "CONTRIBUTOR",
        active_lock_reason: null,
        body: "### Bug report\r\n\r\nThe droppable area of the ListBox is reduced with v 2022.1.119. This is a regression introduced with this version.\r\n\r\n### Reproduction of the problem\r\n1. Run [this dojo](https://dojo.telerik.com/EGOseDoV)\r\n2. Drag an item to the right ListBox\r\n3. Drag another item and drop\r\n\r\n### Current behavior\r\nDropping the item in the ListBox is not successful. Dropping is only successful if you drop over an existing item, the first item in this example.\r\n\r\n### Expected/desired behavior\r\nDropping anywhere in the ListBox should be possible, [as with previous version](https://dojo.telerik.com/AnixebIT)\r\n\r\n### Environment\r\n\r\n* **Kendo UI version:** 2022.1.119\r\n* **Browser:** [all] \r\n\r\n\r\n",
