        body: "### Bug report\r\n\r\nGrid throws an error when the default behavior of the pdf export event is prevented.\r\n\r\n### Reproduction of the problem\r\n1. Run this [dojo](https://dojo.telerik.com/@alexander64836/UPAyaWuS/6)\r\n2. Click the 'Export to PDF' button\r\n\r\n### Current behavior\r\nInspect the console tab and note the submitted error:\r\n![image](https://user-images.githubusercontent.com/92315320/151380143-15fae61e-ae5d-49f2-a013-a7a6417fd60c.png)\r\n\r\n\r\n### Expected/desired behavior\r\nNo error should be thrown\r\n\r\n### Environment\r\n\r\n* **Kendo UI version:** 2022.1.119\r\n* **Browser:** [all] \r\n\r\n\r\n",
