        author_association: "CONTRIBUTOR",
        active_lock_reason: null,
        body: "### Bug report\r\n\r\nWhen the DateInput is part of a form, upon form submission if no value is entered the placeholder is submitted. It would be useful if the DateInput provides a configuration option to unmask on post, similar to the MaskedTextBox [unmaskOnPost configuration option](https://docs.telerik.com/kendo-ui/api/javascript/ui/maskedtextbox/configuration/unmaskonpost).\r\n\r\n### Reproduction of the problem\r\n1. Run this [dojo](https://dojo.telerik.com/@AleksandarEvangelatov/IHifIYIk)\r\n2. Do not enter any values\r\n3. Press submit\r\n\r\n### Current behavior\r\nInspect the Network tab and note the value submitted for Time:\r\n`Time: hours:minutes:seconds`\r\n\r\n### Expected/desired behavior\r\nNo value should be submitted\r\n\r\n### Environment\r\n\r\n* **Kendo UI version:** 2022.1.119\r\n* **Browser:** [all] \r\n\r\n\r\n",
