        author_association: "NONE",
        active_lock_reason: null,
        body: "## Bug report\r\n\r\nReported in 1550223\r\n\r\nThe Scatter chart crashes the browser in the latest versions. Last working version is Kendo UI R3 2021(2021.3.914).\r\n\r\n## Reproduction of the problem\r\n\r\nDojo: https://dojo.telerik.com/@gdenchev/AbIpoBOh (there's also an example in the ticket itself).\r\n\r\nTry to zoom-in the chart with the latest version. The browser window will crash.\r\nTry to zoom-in the chart with v2021.3.914 or earlier. The chart operates as expected.\r\n\r\n## Expected/desired behavior\r\n\r\nZooming the Chart should not crash the browser.\r\n\r\n## Environment\r\n\r\nKendo UI version: 2022.1.119\r\njQuery version: 1.12.3\r\nBrowser: [all] (some additional information about the browsers can be found in the ticket as well)",
