        author_association: "CONTRIBUTOR",
        active_lock_reason: null,
        body: "### Bug report\r\n\r\nhttps://github.com/telerik/kendo-ui-core/blob/master/src/kendo.numerictextbox.js#L2 does not load html/button\r\n\r\n```\r\n(function(f, define){\r\n    define([ './kendo.core', './kendo.userevents', './kendo.floatinglabel' ], f);\r\n})(function(){\r\n```\r\n\r\nthen `html = kendo.html` is undefined at https://github.com/telerik/kendo-ui-core/blob/master/src/kendo.numerictextbox.js#L17\r\n\r\nthen `html.renderButton` fails at https://github.com/telerik/kendo-ui-core/blob/master/src/kendo.numerictextbox.js#L959\r\n\r\n### Note\r\n\r\nRelated to #6683.\r\n\r\n### Reproduction of the problem\r\n\r\nLoad kendo.numerictextbox with an AMD Loader and initialize\r\n\r\n### Current behavior\r\n\r\n```\r\njquery-3.6.0.js:4050 jQuery.Deferred exception: Cannot read properties of undefined (reading 'renderButton') TypeError: Cannot read properties of undefined (reading 'renderButton')\r\n    at buttonHtml (http://localhost:63342/My.Project/src/js/vendor/kendo/kendo.numerictextbox.js:684:25)\r\n    at init._arrows (http://localhost:63342/My.Project/src/js/vendor/kendo/kendo.numerictextbox.js:266:32)\r\n    at new init (http://localhost:63342/My.Project/src/js/vendor/kendo/kendo.numerictextbox.js:69:22)\r\n    at Object.kendo.initWidget (http://localhost:63342/My.Project/src/js/vendor/kendo/kendo.core.js:2473:26)\r\n    at bindingTargetForRole (http://localhost:63342/My.Project/src/js/vendor/kendo/kendo.binder.js:1412:32)\r\n    at bindElement (http://localhost:63342/My.Project/src/js/vendor/kendo/kendo.binder.js:1450:26)\r\n    at bindElement (http://localhost:63342/My.Project/src/js/vendor/kendo/kendo.binder.js:1503:21)\r\n    at bindElement (http://localhost:63342/My.Project/src/js/vendor/kendo/kendo.binder.js:1503:21)\r\n    at Object.bind (http://localhost:63342/My.Project/src/js/vendor/kendo/kendo.binder.js:1514:21)\r\n    at HTMLDocument.<anonymous> (http://localhost:63342/My.Project/src/js/editors/editors.template.html?_ijt=5ra1124ou3ahh33g4dq69ribid:43:27) undefined\r\n```\r\n\r\n### Environment\r\n\r\n* **Kendo UI version:** 2022.1.119\r\n* **jQuery version:** 3.6.0\r\n* **Browser:** all\r\n\r\n",
