        author_association: "CONTRIBUTOR",
        active_lock_reason: null,
        body: "### Bug report\r\n\r\nReported in 1535002\r\n\r\nWhen a selection is made by dragging the mouse over the Grid and then rows are deselected through the checkbox selection, the unchecked rows are also exported.\r\n\r\n### Reproduction of the problem\r\n\r\nDojo: https://dojo.telerik.com/inukEneV\r\n\r\n1. Select all of the rows by holding and dragging the mouse across the grid.\r\n2. Unselect some of the rows by clicking on their checkboxes.\r\n3. Click on the export button - the exported file contains all of the rows, including the ones that were unchecked. This behavior occurs only when mixing drag selection + checkbox deselection.\r\n\r\n### Current behavior\r\n\r\nThe rows that are not selected are exported to the Excel file.\r\n\r\n### Expected/desired behavior\r\n\r\nOnly the selected rows should be exported.\r\n\r\n### Environment\r\n\r\n* **Kendo UI version:** 2022.1.119\r\n* **jQuery version:** 1.12.3\r\n* **Browser:** [all] \r\n\r\n\r\n",
