        author_association: "CONTRIBUTOR",
        active_lock_reason: null,
        body: "### Bug report\r\n\r\nTicket: 1550690\r\n\r\nThe scenario is as follows:\r\n\r\nThe grid uses Endless scrolling. \r\nThere's one locked column present.\r\nThere's a groupfootertemplate with an aggregate which is applied to the locked column.\r\nA column is grouped and filtered(it doesn't matter which column, however both grouping and filtering must be applied to the same one).\r\n\r\n### Reproduction of the problem\r\n\r\nDojo:\r\nhttps://dojo.telerik.com/@gdenchev/uhAVinUm\r\n\r\n1. Group the FirstName column.\r\n2. Scroll down to the groupfootertemplate of the 'Andrew' group. You'll see 108 as the count.\r\n3. Apply a filter to the FirstName column. For example 'Andrew'.\r\n4. Scroll down and the aggregate is missing.\r\n5. Remove the filter from the FirstName column and apply it to a different column, for example LastName. You can use 'Davolio' as a sample value for the filter. The aggregate in the footer is displayed.\r\n\r\n### Current behavior\r\n\r\nThe aggregate is missing from the groupfootertemplate when grouping and filtering is applied to the same column.\r\n\r\n### Expected/desired behavior\r\n\r\nThe aggregate should be displayed regardless of the column that is filtered.\r\n\r\n### Environment\r\n\r\n* **Kendo UI version:** 2022.1.119\r\n* **jQuery version:** 1.12.3\r\n* **Browser:** [all] \r\n\r\n\r\n",
