        body: "### Bug report\r\n\r\nInitializing a Kendo Button from a `<button type='submit></button>` overrides the type attribute. \r\nThis is a regression introduced with v2022.1.119\r\n\r\n### Reproduction of the problem\r\n1. Run this [dojo](https://dojo.telerik.com/@AleksandarEvangelatov/uSIhEkUQ)\r\n1. Inspect the generated markup\r\n\r\n### Current behavior\r\n```\r\n<button id='primaryTextButton' type='submit'>Primary Button</button>\r\n\r\n        <script>\r\n          $(document).ready(function () {\r\n            $('#primaryTextButton').kendoButton({\r\n              themeColor: 'primary'\r\n            });\r\n\r\n          });\r\n        </script>\r\n```\r\nThe generated button has a `type='button'`\r\n```\r\n<button id='primaryTextButton' type='button' data-role='button' class='k-button k-button-md k-button-rectangle k-rounded-md k-button-solid k-button-solid-primary' role='button' aria-disabled='false' tabindex='0'>\r\n    <span class='k-button-text'>Primary Button</span>\r\n</button>\r\n```\r\n\r\n### Expected/desired behavior\r\nType property should  correctly set the button's type to 'submit'\r\n[Dojo v2021.3.1207](https://dojo.telerik.com/@AleksandarEvangelatov/oMuluHEc)\r\n\r\n### Environment\r\n\r\n* **Kendo UI version:** 2022.1.119\r\n* **Browser:** [all] \r\n\r\n\r\n",
