        active_lock_reason: null,
        body: "### Bug report\r\nThe height of the DropDownList popup is incorrect when using LESS themes.\r\n\r\nRegression introduced with version 2022.1.119.\r\n\r\n### Reproduction of the problem\r\n1. Open this Dojo example - https://dojo.telerik.com/@martin.tabakov@progress.com/oMohisUF\r\n2. Open the DropDownList\r\n### Current behavior\r\n\r\nThe popup height is incorrect. \r\n\r\n### Expected/desired behavior\r\nThe popup height should be big enough to fit the list.\r\n\r\n### Environment\r\n\r\n* **Kendo UI version:** 2022.1.119\r\n* **Browser:** [all] \r\n\r\n\r\n",
