        body: "### Bug report\r\n\r\nWhen deleting a record in a grouped 'inCell' editable Grid, the 'Destroy' Action method is not triggered.\r\n\r\n### Reproduction of the problem\r\n\r\n[Attached is a runnable sample](https://feedback.telerik.com/aspnet-core-ui/1550461-operation-destroy-does-not-work-properly-when-the-data-is-grouped). When the Grid's data is not grouped, the records are removed properly.\r\n\r\n### Environment\r\n\r\n* **Kendo UI version: 2022.1.119\r\n* **jQuery version: 1.12.4\r\n* **Browser: [all]",
