        author_association: "CONTRIBUTOR",
        active_lock_reason: null,
        body: "### Bug report\r\n\r\nImproper rounding with ROUND function. The following expression: `=ROUND(35.355, 2)` will result in `35.35` while it should be `35.36`.\r\n\r\nThe issue is caused by the fact that in JavaScript:\r\n```js\r\n35.355 * 100 = 3535.4999999999995\r\n```\r\n\r\n### Reproduction of the problem\r\nUse the following formula in a Spreadsheet: `=ROUND(35.355, 2)`\r\n\r\n### Expected/desired behavior\r\nThe result should be `35.36`\r\n\r\n### Environment\r\n* **Kendo UI version:** 2022.1.119\r\n* **Browser:** [all] ",
