        closed_at: null,
        author_association: "NONE",
        active_lock_reason: null,
        body: "### Bug report\r\nI have a problem related to exporting Excel from a gridview using the ExportToXlsx function. It works fine but in some cases the generated excel 'mixes' header and row data\r\n\r\n### Reproduction of the problem\r\nunfortunately the behavior is quite strange as even with large amounts of data it works correctly but sometimes it generates excel where the header and data content is mixed without any criteria\r\n\r\n`public void Export()\r\n        {\r\n            string extension = 'xlsx';\r\n            var dialog = new SaveFileDialog();\r\n            dialog.DefaultExt = extension;\r\n            dialog.Filter = String.Format('{1} files (*.{0})|*.{0}|All files (*.*)|*.*', extension, 'Excel');\r\n            dialog.FilterIndex = 1;\r\n\r\n            if (dialog.ShowDialog() == true)\r\n            {\r\n                using (var stream = dialog.OpenFile())\r\n                {\r\n                    var exportOptions = new GridViewDocumentExportOptions();\r\n                    exportOptions.ShowColumnFooters = true;\r\n                    exportOptions.ShowColumnHeaders = true;\r\n                    exportOptions.ShowGroupFooters = true;\r\n\r\n                    grdPag.ExportToXlsx(stream, exportOptions);\r\n                }\r\n            }\r\n        }`\r\n\r\n### Current behavior\r\n_(optional)_\r\nProvide additional information if the steps for reproducing the faulty behavior are not sufficient to describe the issue.\r\n\r\n### Expected/desired behavior\r\nthe generated excel is a replica of the grid view\r\n\r\n### Environment\r\nTelerik.Windows.Controls.GridView.Export, Version=2015.1.401.40, Culture=neutral, PublicKeyToken=5803cfa389c90ce7\r\nBrowser: Chrome Version 97.0.4692.99 (Official Build) (64-bit)\r\n\r\n\r\n",
