            following_url:
                "https://api.github.com/users/mphilip94/following{/other_user}",
            gists_url: "https://api.github.com/users/mphilip94/gists{/gist_id}",
            starred_url:
                "https://api.github.com/users/mphilip94/starred{/owner}{/repo}",
            subscriptions_url:
                "https://api.github.com/users/mphilip94/subscriptions",
            organizations_url: "https://api.github.com/users/mphilip94/orgs",
            repos_url: "https://api.github.com/users/mphilip94/repos",
            events_url:
                "https://api.github.com/users/mphilip94/events{/privacy}",
            received_events_url:
                "https://api.github.com/users/mphilip94/received_events",
            type: "User",
