        updated_at: "2022-01-19T12:48:04Z",
        closed_at: null,
        author_association: "CONTRIBUTOR",
        active_lock_reason: null,
        body: "### Bug report\r\n\r\nTreeList DataSource helpers do not expose an option to configure the DataSource for WebAPI or oData binding.\r\n\r\n### Reproduction of the problem\r\nThe TreeList DataSource does not expose a .Type() configuration:\r\n```\r\n@(Html.Kendo().DataSource<MyViewModel>()\r\n        .Name('datasource')\r\n        .TreeList(source =>\r\n        {\r\n            source.Batch(true);\r\n            source.ServerOperation(false);\r\n            source.Model(model =>\r\n            {\r\n                model.Id(x => x.Id);\r\n                model.ParentId(x => x.Parent);\r\n                model.Expanded(x => x.Expanded);\r\n            });\r\n            source.Read('Read', 'Api');\r\n            source.Create(ajax => ajax.Action('Write', 'Api').Type(HttpVerbs.Post));\r\n            source.Update(ajax => ajax.Action('Write', 'Api').Type(HttpVerbs.Put));\r\n            source.Destroy(ajax => ajax.Action('Write', 'Api').Type(HttpVerbs.Delete));\r\n        })\r\n)\r\n```\r\n\r\n\r\n### Workaround\r\nCurrently, this can be achieved by defining an external dataSource using Kendo UI for jQuery and passing this dataSource to the TreeList helper, as demonstrated in the [oData binding](https://demos.telerik.com/aspnet-core/treelist/odata) demo\r\n\r\n### Environment\r\n\r\n* **Kendo UI version:** 2021.3.1207\r\n* **Browser:** [all] \r\n\r\n\r\n",
