        author_association: "NONE",
        active_lock_reason: null,
        body: "### Bug report\r\nThe DropDownTree component with remote dataSource and checkboxes doesn't preserve user selection after dataSource.read() call and breaks the selected items row by not allowing a user to remove previously selected items.\r\n\r\n### Reproduction of the problem\r\n1. Open the [Dojo reproduction page](https://dojo.telerik.com/@astarostin/UliYeHUf)\r\n2. Run the code\r\n3. Click on the buttons according to their order (1,2,3,4)\r\n4. Try to cycle through 1->2->3 several times to see that the selection is kept in the dropdowntree row.\r\n\r\n### Current behavior\r\nOnce dataStore.read() is called to get the latest data, all checkboxes will be deselected and the selection row will keep the previously selected data. The selection row cannot be altered using the close button.\r\n\r\n### Expected/desired behavior\r\nOnce dataStore.read() is called, user selection should be preserved and the fresh data should be populated to the dropdowntree.\r\nA user should be able to alter the selected checkboxes just like before refreshing the dataStore.\r\n\r\n### Environment\r\n* **Kendo UI version:** 2021.3.1207\r\n* **Kendo ASP.NET MVC version:** 2020.3.915\r\n* **Browser:** [Chrome 97 | Firefox 95]  (might be all)\r\n\r\n\r\n",
