        author_association: "CONTRIBUTOR",
        active_lock_reason: null,
        body: "related to:\r\nhttps://github.com/telerik/kendo-themes/issues/2961\r\n\r\n### Bug report\r\n\r\nContextMenu is not scrollable with SASS themes. The arrow for scrolling is missing.\r\n\r\n1. Open in Dojo the [scrollable](https://docs.telerik.com/kendo-ui/api/javascript/ui/contextmenu/configuration/scrollable) example in the ContextMenu API\r\n1. Right-click to see the ContextMenu\r\n\r\n**Expected behavior**\r\nThe ContextMenu should be scrollable with SASS themes when the scrollable option is enabled.\r\n\r\n**Screenshots**\r\n![image](https://user-images.githubusercontent.com/14364791/141467599-5aab293d-84e4-445b-8de0-420edf6b454e.png)\r\n\r\n### Current behavior\r\nCurrently, the ContextMenu is not scrollable when SASS themes are used.\r\n\r\n**Additional context**\r\nRegression since version: 2020.2.513\r\n\r\n### Environment\r\n\r\n* **Kendo UI version:** 2021.3.1207\r\n* **Browser:** [all] \r\n\r\n\r\n",
