        author_association: "CONTRIBUTOR",
        active_lock_reason: null,
        body: "### Bug report\r\nScheduler events are not properly positioned in mobile RTL scenarios when a scrollbar is displayed and grouping is enabled\r\n\r\n### Reproduction of the problem\r\n- Run the following Dojo: https://dojo.telerik.com/UZiZoNop/16\r\n- Notice that only Ev1 is visible under resource 'one'. It, however, should belong to resource 18 (the leftmost column). If you inspect the markup you will notice that all events are positioned to the right (outside the Scheduler visible element).\r\n\r\n### Expected/desired behavior\r\nEvents should be positioned properly in the groups they belong to.\r\n\r\n### Environment\r\n* **Kendo UI version:** 2021.3.1207\r\n* **Browser:** [all] ",
