        closed_at: "2022-02-02T14:44:02Z",
        author_association: "NONE",
        active_lock_reason: null,
        body: "### Bug report\r\n\r\nWhen we have `checkChidren` set to true under `checkboxes` option for kendo **dropdowntree**, clicking on parent label in treeview is creating duplicate IDs for the self (parent) and it's children.\r\n\r\n### Reproduction of the problem\r\nDojo : [https://dojo.telerik.com/EjOVIlep](https://dojo.telerik.com/EjOVIlep).\r\n### Current behavior\r\n> Right now whenever the parent label is clicked, duplicate IDs are getting created for the self and for its  childrens. But the same isn't happening when we click input checkbox.\r\n\r\n### Expected/desired behavior\r\n> Duplicate ID's shouldn't be generated.\r\nDojo(2021.3.914) : https://dojo.telerik.com/EjOVIlep/11\r\n\r\n\r\n### Environment\r\n\r\n* **Kendo UI version:** 2021.3.1207 (Reproducible since 2021.3.1109)\r\n* **jQuery version:** 1.12.4\r\n* **Browser:** '5.0 (Windows NT 10.0; Win64; x64) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/96.0.4664.110 Safari/537.36 Edg/96.0.1054.62'\r\n\r\n\r\n\r\n",
