        closed_at: null,
        author_association: "NONE",
        active_lock_reason: null,
        body: "Kendo UI Scheduler\r\nReproduction of the problem\r\nWhen a recurrence rule is created, the recurrence is incorrect after the passing over to a new year.\r\n\r\nExample: The recurrence start date 2021-12-14 and recurrenceRule 'FREQ=WEEKLY;INTERVAL=2;BYDAY=TU'\r\n\r\n![image](https://user-images.githubusercontent.com/89107468/147657776-64f8a9ed-3ff4-4a45-a7ff-4d7d8ec54f05.png)\r\n![image](https://user-images.githubusercontent.com/89107468/147657804-d9110f06-c6e6-430a-af0f-243e0961e492.png)\r\n![image](https://user-images.githubusercontent.com/89107468/147657825-857461e6-4ebe-4119-8bda-11ee62ce458c.png)\r\n![image](https://user-images.githubusercontent.com/89107468/147657838-df302775-535a-46f7-a04a-af6d8881a1aa.png)\r\n![image](https://user-images.githubusercontent.com/89107468/147657862-66c5c93b-2623-46b3-9277-217c0441e586.png)\r\n\r\nhttp://dojo.telerik.com/EzUHeSuV\r\n\r\nCurrent behavior\r\nEvent dates are dec 14, dec 28, jan 4, jan 18, ..\r\n\r\nExpected/desired behavior\r\nEvent dates should be dec 14, dec 28, jan 11, jan 25,..\r\n\r\nSeems this issue happens every 11 years. (Dec 2010 to Jan 2011, Dec 2021 to Jan 2022, Dec 2031 to Jan 2032)\r\nI can't change the kendo version. It's possible have a workaround for this issue?\r\n\r\nEnvironment\r\nKendo UI version: 2016.3.1028\r\njQuery version: 1.12.3\r\nBrowser: Opera 82.0.4227.33",
