        author_association: "CONTRIBUTOR",
        active_lock_reason: null,
        body: "### Bug report\r\nCreating a TreeListDataSource instance using the HTML Helpers does not set the DataSource type. \r\n\r\n### Reproduction of the problem\r\n1. Define a TreeListDataSource\r\n```\r\n@(Html.Kendo().DataSource<Kendo.Mvc.Examples.Models.TreeList.EmployeeDirectoryRemoteModel>()\r\n    .Name('treedata')\r\n    .TreeList(dataSource => dataSource\r\n        .ServerOperation(true)\r\n        .Read(read => read.Action('Index', 'EmployeeDirectory'))\r\n        .Model(m => {\r\n            m.Id(f => f.EmployeeId);\r\n            m.ParentId(f => f.ReportsTo);\r\n            m.Field(f => f.FirstName);\r\n            m.Field(f => f.LastName);\r\n            m.Field(f => f.ReportsTo);\r\n        })\r\n    )\r\n)\r\n```\r\n2. Inspect the generated script and note the type is not set to `aspnetmvc-ajax` - [Example REPL](https://netcorerepl.telerik.com/mPbQbhOi08QHpSMa57). Defining the DataSource as part of the TreeList component sets the type as expected - [TreeList Remote DataBinding Demo](https://demos.telerik.com/aspnet-core/treelist/remote-data-binding)\r\n\r\n### Expected/desired behavior\r\nTreeListDataSource instance defined as a standalone component or as part of the TreeList component should have the same configuration.\r\n\r\n### Workaround\r\nIf you need to use an external DataSource with an ASP.NET Core TreeList HTML Helper configure the [TreeListDataSource](https://docs.telerik.com/kendo-ui/api/javascript/data/treelistdatasource) using Kendo UI for jQuery:\r\n```\r\nvar TreeListDataSource = new kendo.data.TreeListDataSource({\r\n        type: 'aspnetmvc-ajax',\r\n        transport: {\r\n           //transport configuration\r\n        },\r\n        schema: {\r\n          //schema configuration\r\n        }\r\n       //additional configuration options\r\n})\r\n```\r\n### Environment\r\n\r\n* **Kendo UI version:** 2021.3.1207\r\n* **Browser:** [all] \r\n\r\n\r\n",
